@mixin flex-row-start{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
}
@mixin flex-row-end{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: row;
}
@mixin flex-column-start{
    display: flex;
    justify-content:flex-start;
    align-items: flex-start;
    flex-direction: column;
}

@mixin flex-column-between{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
}
@mixin flex-row-center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row
}
@mixin flex-row-between {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row
}
@mixin flex-row-evently {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row
}

@mixin flex-column-center{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

@mixin  responsive ($breakpoint) {
    @if $breakpoint == bigscreen {
        @media only screen and (max-width: 1200px){@content};
        }
    
@if $breakpoint == laptop {
@media only screen and (max-width: 1024px){@content};
}

      @if $breakpoint == tab {
      @media only screen and (max-width: 800px) {@content};
       }
           @if $breakpoint == mobile  {
        @media only screen and (max-width: 500px) {@content};
         }

         @if $breakpoint == bigmobile {
            @media only screen and (max-width: 660px) {@content};
                          }
          
         @if $breakpoint == minmobile {
             @media only screen and (max-width: 400px) {@content};
} 
}
